export const sliceData = (param)=>{
    let { size , additional , existing , startIndex  } = param;
    const remaining = existing.length - (startIndex + size);
    return [
        ...Array.from(Array(startIndex).keys()),
        ...additional,
        ...(remaining > 0 ? Array.from(Array(remaining).keys()) : []).map((key)=>key + startIndex + additional.length), 
    ];
};
export const orderByFn = (arr, funcs, dirs)=>[
        ...arr
    ].sort((rowA, rowB)=>{
        if (!Object.keys(rowA.original).length || !Object.keys(rowB.original).length) {
            return 0;
        }
        for(let i = 0; i < funcs.length; i += 1){
            const sortFn = funcs[i];
            const desc = dirs[i] === false || dirs[i] === "desc";
            const sortInt = sortFn(rowA, rowB);
            if (sortInt !== 0) {
                return desc ? -sortInt : sortInt;
            }
        }
        return dirs[0] ? rowA.index - rowB.index : rowB.index - rowA.index;
    });
